#navbar {
  /* padding: 15px 0px 15px 20px; */
  display: flex;
  justify-content: space-between;
  box-shadow: 2px 0 6px rgba(0, 0, 0, 0.5);
  background-color: white;
  transition: background-color 0.6s;
  min-height: 56px;
  position: fixed;
  width: 100%;
  top: 0;
  align-items: center;
  z-index: 10;
  transition: opacity 0.5s;
}
#navbar:hover {
  opacity: 1;
}

#navbar .left a {
  font-size: large;
  font-weight: bold;
  text-decoration: none;
  color: black;
  transition: color 0.3s;
  margin-left: 20px;
}

#navbar .left a:hover {
  transform: scale(1.05);
  transition: 0.2s;
}

#navbar .right {
  display: flex;
  /* width: 25%; */
  justify-content: space-around;
}
#navbar .right a {
  text-decoration: none;
  color: black;
  margin: 0 10px;
  transition: color 0.3s;
}
#navbar .right a:hover {
  /* border-bottom: 1px solid black; */
  text-decoration: underline;
  transition: 1s;
}

.themeBtn {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 12px;
  border-radius: 50vmax;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3);
  margin: 0 10px 0 20px;
}
.themeBtn:active {
  transform: scale(0.95);
}
.themeBtn svg {
  color: #333;
}
.themeBtn.mobile {
  display: none;
}
@media (max-width: 600px) {
  #navbar {
    flex-direction: column;
  }
  #navbar .left {
    display: flex;
    justify-content: space-between;
    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.5);
    width: 100%;
    padding: 6px 0;
  }
  #navbar .right {
    padding: 6px 0;
  }
  .themeBtn {
    display: none;
  }
  .themeBtn.mobile {
    display: flex;
  }
}
